
import './App.css';
import SuggestBox from './SuggestBox';
import React from 'react';
import DicBox from './DicBox';
import Modal from './Modal';

class App extends React.Component {
  constructor(){
    super();
    this.state={
      value:'',
      lastWord:'',
      selectedWord:'none',
      showModal:true,
      textAreaStyle:{fontColor:'black'}
    }

    this.handleChange = this.handleChange.bind(this);
    this.setSelectedWord=this.setSelectedWord.bind(this);
    this.saveFile=this.saveFile.bind(this);

  }

  saveFile(){
    if(this.state.value.charAt(0)!==' ' && this.state.value.charAt(0)!==null)
    localStorage.setItem("text",this.state.value);
    if(this.state.showModal==true){
    this.setState({showModal:false});}else
    {this.setState({showModal:true});}
  }
  setSelectedWord(word){
    this.setState({selectedWord:word});
  }
  handleChange(event) {
    this.setState({value: event.target.value});
  }
  componentDidUpdate(prevProps,prevState){
    if(prevState.value!==this.state.value){
    var n = this.state.value.split(" ");
    var lastValue=n[n.length-1];
    if(lastValue!=='')
    this.setState({lastWord:lastValue,textAreaStyle:{fontColor:'black'}});
    }
  }
  componentDidMount(){
    let locStore=localStorage.getItem("text");
    if(locStore!=null)
      this.setState({value:locStore});

  }
render(){
  return (
    <>
    <div style={{display:"flex",justifyContent:"space-between",border:"solid",paddingRight:"1em",marginBottom:"20px",backgroundColor:"black"}} >
    <h1 className='title' style={{color:"white"}}>Poetry Pad</h1><button onClick={this.saveFile} style={{backgroundColor:"white",color:"black",margin:"10px",padding:'auto',borderColor:'black',fontWeight:"bold",fontSize:"1em"}}>SAVE</button>
    </div>
    <div className="App">
  <textarea type="text" value={this.state.value} placeholder="Write Something here..."  style={this.state.textAreaStyle} onChange={this.handleChange} />
      <DicBox word={this.state.selectedWord}/>
      <SuggestBox setSelectedWord={this.setSelectedWord} lastWord={this.state.lastWord}/>
       <Modal showModal={this.state.showModal}/>
    </div>
    </>
  );}
}

export default App;
