import './App.css';
import React from 'react';
import LoadingBlob from './LoadingBlob';

class DicBox extends React.Component{
    constructor(props){
        super(props);
        this.state={
            definition:'',
            word:'',
            type:'none',
            loading:false
        }
    }
   async componentDidUpdate(prevProps){
        if(prevProps.word!==this.props.word){
            this.setState({loading:true});
            fetch(`https://mashape-community-urban-dictionary.p.rapidapi.com/define?term=${this.props.word}`, {
                "method": "GET",
                "headers": {
                    "x-rapidapi-key": "e2d0c94a15msh9ede8aff31b4315p1f8806jsnf67c8d0c84c8",
                    "x-rapidapi-host": "mashape-community-urban-dictionary.p.rapidapi.com"
                }
            })
            .then(response => {
                this.setState({loading:false});
               return response.json();
            }).then(data=>this.setState({definition:data.list[0].definition,word:data.list[0].word}))
            .catch(err => {
                this.setState({loading:false});
                this.setState({definition:err.toString(),word:this.props.word});
            }
            );
        }
   }

   render(){
       return(<>

{(!this.state.loading)?
    <div className='dicBox' style={{overflow:"scroll",overflowX:"hidden",fontSize:"small",boxSizing:"border-box",paddingTop:"0em",padding:"1em",textAlign:"left",border:"solid black thin"}}>

        <h1>{this.state.word}</h1>
       <p>{this.state.definition}</p>
       
        </div>:<div className='dicbox'   id='dicboxLoading'><LoadingBlob loadOrStart='load' /></div>}
            </>
       )
   }
}

export default DicBox;