
import './App.css';
import React from 'react';

class SuggestBox extends React.Component {
  constructor(props){
    super(props);
    this.state={
      suggested:[],
      selected:-1,
    }
    this.handleChange = this.handleChange.bind(this);
    this.setSelected =this.setSelected.bind(this);
  }
  componentDidUpdate(prevProps) {
 
    if (this.props.lastWord !== prevProps.lastWord) {
        fetch(`https://rhymebrain.com/talk?function=getRhymes&word=${this.props.lastWord}`)
        .then(response => response.json())
        .then(data => this.setState({suggested:data}));
    }
  }
  handleChange(event) {
    this.setState({value: event.target.value});
  }

  setSelected(newIndex,element){
    this.setState({selected:newIndex});
    this.props.setSelectedWord(element);
  }
 
render(){
 
  
  return (
 
        <ul>{this.state.suggested.map((el,index) =>
      (this.state.selected===index)?<li onClick={()=>this.setSelected(index,el.word)} style={{background:"#fafafa",fontWeight:"bold"}} id={index}>{el.word}</li>:<li onClick={()=>this.setSelected(index,el.word)} id={index}>{el.word}</li>
)}</ul>
    
  );}
}

export default SuggestBox;
